<script setup lang="ts">
import { useRoute } from "vue-router";
import { fetchIllustrationsId } from "@/illustrationService";
import { IllustratedStoryType } from "@/types/Illustration";
import { ref, onMounted } from "vue";

const route = useRoute();
const id = route.params.id as string;
const loading = ref(true);

let illustration = {} as IllustratedStoryType;

onMounted(async () => {
  console.log("Fetching illustration...");
  try {
    console.log("Fetching illustration...");
    illustration = await fetchIllustrationsId(Number(id));
    console.log("Illustration", illustration);
  } catch (error) {
    console.error("Error fetching illustration", error);
  } finally {
    loading.value = false;
  }
});
</script>

<template>
  <div v-if="loading">Loading illustration...</div>
  <div v-else class="group">
    <h3 class="">
      {{ illustration.title }}
    </h3>
    <img
      class="illustration"
      :src="require(`@/assets/${illustration.imageUrl}`)"
      :alt="illustration.title"
    />
    <div class="group-text">
      <p>{{ illustration.description }}</p>
    </div>
  </div>
</template>

